import {useEffect, useState}from "react";
//dependencias
//import * as commentaryServer from "../../services/commentary";
import { useModal } from "../../hooks/useModal";
import ModalComentario from "./comentarioModal";

//components
import { makeStyles } from "@material-ui/core/styles";
import Modal from "react-bootstrap/Modal";
import Container from "@material-ui/core/Container";
import { Button } from "@mui/material";
import Stack from '@mui/material/Stack';


const useStyles = makeStyles((theme) => ({
  title:{
    color: "black",
    textAlign: "center",
  },
  nameUser:{
    color:"Gray",
    fontSize:"15px"
  },
  containerModal:{
    boxShadow: "4px 4px 4px 0px #5a28e550",
  },
  containerComment: {
    maxWidth: 720,
    margin: "auto",
    marginBottom: 20,
  },
  "@media (max-width: 720px)": {
    root: {
      display: "flex",
    },
  },
}));

const ModalComentarios = ({ handleClose, show, histUser, commentaries,  ...props}) => {
  const [showEdit, handleShowEdit, handleCloseEdit] = useModal(false);
  const classes = useStyles();
  
  if (commentaries) {
    return (
    <Modal 
      show={show} 
      onHide={handleClose}
      aria-labelledby="example-custom-modal-styling-title"
      contentClassName = {classes.containerModal}
      centered
      scrollable
      
    >
      <Modal.Header closeButton  >
        <Modal.Title id="titleModal" className={classes.title} >Comentarios de usuarios</Modal.Title>
      </Modal.Header>
      <Modal.Body id="bodyModal" className={classes.containerModal}>
      {commentaries && commentaries.length > 0 ? (
  commentaries.map((element, index) => (
    <Container key={index} className={classes.containerComment}>
      <div className="row">
        <div className="col-9">
          <p>{new Date(element.created_date).toLocaleDateString()}</p>
          <h5 className={classes.nameUser}>{element.historial_user.usuario.name}</h5>
          <h6>{element.commentary}</h6>
        </div>
        <div className="col-2">
          {element.historial_user.usuario.id === histUser?.usuario?.id && (
            <Stack alignItems="center">
              <Button 
                variant="contained" 
                color="info"
                onClick={handleShowEdit}>
                EDITAR 
              </Button>
              <ModalComentario
                handleClose={handleCloseEdit}
                show={showEdit}
                commentary_id={element.id}
              />
            </Stack>
          )}
        </div>
        <hr/>
      </div>
    </Container>
  ))
) : (
  <p className="text-center text-muted">No hay comentarios aún.</p>
)}

      </Modal.Body>
    </Modal>
    )
  }
  
};

export default ModalComentarios;