import { useEffect, useState, useCallback, useRef } from "react";
import { useLocation, useParams } from "react-router-dom";
import Player from "@vimeo/player";

// Servicios
import * as UbicacionServer from "../../services/ubicacion";
import * as FechareproServer from "../../services/fechaRepro";
import * as HistorialUserServer from "../../services/historialUser";
import * as HistorialVideoServer from "../../services/historialVideo";

import "../../styles/styles.css";

const URL_API = "https://ipapi.co/json/";

const IframeVideo = ({ video, webinarCondition}) => {
  const [ubicacionUsers, setUbicacionUsers] = useState(null);
  const [histUser, setHistUser] = useState(null);
  const [histVideo, setHistVideo] = useState(null);
  const [loading, setLoading] = useState(true);
  const { id } = useParams();
  const location = useLocation();
  const iframeRef = useRef(null);
  const intervalRef = useRef(null);

  const getUbication = async () => {
    try {
      const res = await fetch(URL_API);
      const data = await res.json();
      setUbicacionUsers(data);
    } catch (err) {
      console.error("Error al obtener ubicación:", err);
    }
  };

  const getHistUser = async () => {
    try {
      const userId = location?.state?.id || location?.state?.usuario;
      if (!userId) return console.warn("No hay datos suficientes para historial");
      const res = await HistorialUserServer.getHistorial(userId);
      setHistUser(res);
    } catch (error) {
      console.error("Error al obtener historial:", error);
    }
  };

  const getHistorialVideo = async () => {
    const res = await HistorialVideoServer.ListHistorialVideo({ video_id: id });
    if (res.length > 0) {
      setHistVideo(res);
    } else {
      const formData = new FormData();
      formData.append("video", id);
      const nuevo = await HistorialVideoServer.RegisterHistorialVideo(formData);
      setHistVideo(nuevo);
    }
  };

  const convertTime = (tiempo) => {
    let [h, m, s] = tiempo.split(":").map(Number);
    return (h * 3600 + m * 60 + s);
  };

  const obtenerFecha = () => {
    const f = new Date();
    const day = `${f.getDate()}`.padStart(2, "0");
    const month = `${f.getMonth() + 1}`.padStart(2, "0");
    const year = f.getFullYear();
    return `${year}-${month}-${day}`;
  };

  const repetirCada10Segundos = (player) => {
    intervalRef.current = setInterval(() => saveTime(player), 10000);
  };

  const saveTime = async (player) => {
    try {
      const duracion = await player.getDuration();
      const tiempo = await player.getCurrentTime();

      if (tiempo !== 0 && histUser?.id) {
        const validacion = parseInt((duracion * 90) / 100);
        let cont1 = histUser.counter_repro;
        let cont2 = histVideo[0].reproducciones;

        if (tiempo >= validacion) {
          cont1 += 1;
          cont2 += 1;
          await HistorialUserServer.updateHistorialUser(histUser.id, {
            tiempo: 0,
            visto: true,
            counter_repro: cont1,
          });
          await HistorialVideoServer.updateHistorialVideo(histVideo[0].id, {
            reproducciones: cont2,
          });
        } else {
          await HistorialUserServer.updateHistorialUser(histUser.id, {
            tiempo: parseInt(tiempo),
          });
        }
      }
    } catch (error) {
      console.error("Error al guardar tiempo:", error);
    }
  };
  const getVimeoVideo = useCallback(() => {
   
  
    const player = new Player(iframeRef.current, {
      id: video.code_esp,
      playsinline: true,
    });
  
    if (!webinarCondition && histUser?.tiempo) {
  const segundos = convertTime(histUser.tiempo);
  player.setCurrentTime(segundos);
}

  
    if (!webinarCondition) {
      player.on("play", async () => {
        try {
          const ubicaciones = await UbicacionServer.ListUbicacionHist({
            histUser_id: histUser.id,
          });
          const fechaActual = obtenerFecha();
          const dataFecha = new FormData();
          dataFecha.append("historial_user", histUser.id);
          dataFecha.append("historial_Video", histVideo[0].id);
          const fechasRepro = await FechareproServer.ListFechaReprox2(dataFecha);
  
          if (ubicacionUsers) {
            const ubicForm = new FormData();
            ubicForm.append("direccionIP", ubicacionUsers.ip);
            ubicForm.append("ciudad", ubicacionUsers.city);
            ubicForm.append("pais", ubicacionUsers.country);
            ubicForm.append("historial_user", histUser.id);
            ubicForm.append("historial_Video", histVideo[0].id);
  
            const yaExisteUbicacion = ubicaciones.some(
              (u) =>
                u.direccionIP === ubicacionUsers.ip &&
                u.ciudad === ubicacionUsers.city &&
                u.pais === ubicacionUsers.country
            );
  
            if (!yaExisteUbicacion) {
              await UbicacionServer.RegisterUbicacion(ubicForm);
            }
  
            const yaExisteFecha = fechasRepro.some((f) => f.fecha === fechaActual);
  
            if (!yaExisteFecha) {
              await FechareproServer.RegisterFechaRepro({
                historial_user: histUser.id,
                historial_Video: histVideo[0].id,
              });
            }
          }
        } catch (err) {
          console.error("Error en evento play:", err);
        }
      });
  
      if (document.URL.includes("/seeVideo/")) {
        repetirCada10Segundos(player);
      }
    }
    setLoading(false);

  }, [video.code_esp, histUser, histVideo, ubicacionUsers, webinarCondition]);
  

  useEffect(() => {
  getHistUser();
  getHistorialVideo();
  getUbication(); // Si lo necesitas
}, []);

// Llama a getVimeoVideo solo cuando los datos estén listos
useEffect(() => {
  const hayDatosParaReproducir =
    video.code_esp &&
    (webinarCondition || (!webinarCondition && histVideo && ubicacionUsers));

  if (hayDatosParaReproducir) {
    //console.log("Llamando a getVimeoVideo con datos completos");
    getVimeoVideo();
  } else {
    console.log("Esperando datos para getVimeoVideo:")
  }
}, [video.code_esp, histUser, histVideo, ubicacionUsers, webinarCondition, getVimeoVideo]);

  

  return (
    <>
      {loading && (
        <div className="mt-5 d-flex justify-content-center align-items-center">
          <div className="spinner-grow text-light" role="status">
            <span className="sr-only"></span>
          </div>
        </div>
      )}

      <div id="iframe1" ref={iframeRef}></div>
    </>
  );
};

export default IframeVideo;
